<template>
	<div id="userProfile">
		<LoadingSpinner v-if="isLoading"></LoadingSpinner>
		<div class="inner">
			<div class="top">
				<div class="user_img">
					<img :src="`${baseImageUrl}${userInfo.EMAIL}`" alt="" />
				</div>
				<div class="user_info">
					<p class="user_name">
						{{ userInfo.NAME }}
						<span class="small">{{ userInfo.CALL_NAME }}</span>
					</p>
					<p class="user_dept">{{ userInfo.DEPT_NAME }}</p>
				</div>
			</div>
			<div class="btm">
				<ul class="list_wrap">
					<li class="list">
						<span class="icon">
							<font-awesome-icon icon="fa-solid fa-phone" />
						</span>
						<a class="tel" :href="`tel:+${userInfo.HP}`">{{ userInfo.HP }}</a>
					</li>
					<li class="list">
						<span class="icon">
							<font-awesome-icon icon="fa-solid fa-at" />
						</span>
						<a class="email" :href="`mailto: ${userInfo.EMAIL}`">{{
							userInfo.EMAIL
						}}</a>
					</li>
					<li class="list">
						<span class="icon">üéÇ</span>
						<p class="birthday">
							{{ userInfo.EI_BIRTH_DATE | formatFullDate }}
						</p>
					</li>
					<li class="list">
						<span class="icon enter_date">ÏûÖÏÇ¨Ïùº</span>
						<p class="birthday">
							{{ userInfo.ENTERING_DATE | formatFullDate }}
						</p>
					</li>
				</ul>
			</div>
		</div>

		<button type="button" class="btn_back" @click="goBack">
			<font-awesome-icon icon="fa-solid fa-arrow-left" />
		</button>
	</div>
</template>

<script>
import LoadingSpinner from "@/components/common/LoadingSpinner.vue";
import { fetchOneUser } from "@/api/index";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faPhone, faAt, faArrowLeft } from "@fortawesome/free-solid-svg-icons";
library.add(faPhone, faAt, faArrowLeft);
export default {
	components: {
		LoadingSpinner,
	},
	data() {
		return {
			baseImageUrl: process.env.VUE_APP_IMG_URL,
			isLoading: false,
		};
	},
	computed: {
		userInfo() {
			return this.$store.state.arrOneUser;
		},
	},
	methods: {
		goBack() {
			this.$router.go(-1);
		},
	},
	filters: {
		formatDate(value) {
			const date = new Date(value);
			let month = date.getMonth() + 1;
			month = month > 9 ? month : `0${month}`;
			let day = date.getDate();
			day = day > 9 ? day : `0${day}`;
			return `${month}Ïõî ${day}Ïùº`;
		},
	},
	async created() {
		try {
			this.isLoading = true;
			const userId = await fetchOneUser(this.$route.params.id);
			this.$store.commit("setOneUser", userId.data);
			this.isLoading = false;
		} catch (error) {
			console.log(error);
		}
	},
};
</script>

